@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: 'Varela Round', sans-serif;
}

.home, .reports, .products {
    display: flex;
    height: 90vh;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
}

@media (max-width: 775px) {
    .home, .reports, .products {
        font-size: 1.5rem;
        background-color: #2500aa;
        color: #fff;
    }
}
.navbar {
    background-color: #3b4544;
    height: 10vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.brand-logo {
    width: 7em;
    padding: .5em;
}

.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu {
    background-color: #221d1d;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}

.nav-menu.active {
    left: 0;
    transition: 350ms;
}

.nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 0.5em 0 0.5em 1em;
    list-style: none;
    height: 3.75em;
}

.nav-text a {
    text-decoration: none;
    color: #ffffff;
    font-size: 1.15rem;
    width: 95%;
    height: 100%;
    display: grid;
    grid-template-columns: 2fr 10fr;
    grid-template-rows: 1fr;
    padding: .5em;
    margin-left: 1em;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: #4da8ac;
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: #221d1d;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}
.preloader {
    display: flex;
    width: 100%;
    height: 100vh;
    justify-content: center;
    text-align: center;
    align-items: center;
}
